<template>
    <div class='box'>
            <div class='warp'>
                    <header class='top'>
                        <p>可向多个商家咨询最低价，商家及时回复</p>
                    </header>
                    <section>   
                        <div class='cont'>
                            <div class='left'>
                                <img :src="detaiList.Picture" alt="">
                            </div>
                            <div class='right'>
                                <p>{{detaiList.AliasName}}</p>
                                <span>2018款30周年版30TFSI进取版</span>
                            </div>
                        </div>
                        <p class='title'>个人信息</p>
                        <div class='list'>
                            <li>
                                <span>姓名</span>
                                <input type="text" placeholder="输入你的真实中文姓名">
                            </li> 
                            <li>
                                <span>手机</span>
                                <input type="text" placeholder="输入你的真实手机号" ref="phone">
                                <button @click="getCapture" class="button">获取短信验证码</button>
                            </li>
                            <li @click='showCity()'>
                                <span>城市</span>
                            </li>
                        </div>
                       <div class='btn'>
                           <button>询问底价</button>
                       </div>
                        <div class='shop_info'>
                                <p class='title'>选择报价经销商</p>
                            <div class='shop_list'>
                                <li class='list_info' v-for='(item,index) in getList'>
                                    <p class='list_left'>
                                        <span>{{item.dealerShortName}}</span>
                                        <span>{{item.address}}</span>
                                    </p>
                                    <p class='list_right'>
                                        <span>{{item.vendorPrice}}万</span>
                                        <span>{{item.saleRange}}</span>
                                    </p>
                                </li>
                                 <!-- {{getList}} -->
                            </div> 
                        </div>
                    </section>
                </div>
                <City class="showCity?'active':''"></City>
                {{checkCity}}
    </div>
   
</template>
<script>
    import City from './common/City.vue'
    import {
        mapState,
        mapActions,
        mapMutations
    } from 'vuex';
    export default {
        components: {
            City
        },
        computed: {
            ...mapState({
                currentCity: state => state.follor.currentCity,
                getList: state => state.follor.getList,
                detaiList: state => state.detail.detaiList,
                checkCity: state => state.follor.checkCity,
                isShowCity: state => state.follor.isShowCity,
            })
        },
        methods: {
            ...mapActions({
                getDealerLists: 'follor/getDealerLists'
            }),
            ...mapMutations({
                getDealerList: 'follor/getDealerList',
                showCity: 'follor/showCity'
            }),
            getCapture() {
                let phone = this.$refs.phone.value;
                if (!/^1[3,4,5,7,8,9]\d{9}$/.test(phone)) {
                    alert('请输入正确的手机号码');
                    return;
                }
                console.log('获取手机号：', phone);
            }
        },
        mounted() {
            // this.detaiList();
            this.getDealerLists(2, 2)

        }
    }
</script>
<style scoped lang='scss'>
    .box {
        width: 100%;
        height: 100%;
        overflow: hidden;
    }
    
    .warp {
        width: 100%;
        height: 100%;
        overflow: hidden;
        display: flex;
        flex-direction: column;
        position: relative;
    }
    
    .top {
        width: 100%;
        height: .6rem;
        font-size: .3rem;
        background: #79cd92;
        text-align: center;
        line-height: .6rem;
        p {
            color: #fff;
        }
    }
    
    section {
        flex: 1;
        background: #f4f4f4;
        overflow-y: scroll;
    }
    
    .cont {
        display: flex;
        font-size: .28rem;
        .left {
            flex: 3;
            padding: .2rem;
            img {
                width: 100%;
                height: auto;
            }
        }
        .right {
            padding-top: .2rem;
            flex: 7;
            padding-left: .2rem;
            line-height: 2;
        }
    }
    
    .title {
        padding: 0 .2rem;
        height: .6rem;
        line-height: .6rem;
        font-size: .24rem;
        color: #666;
        background: #eee;
    }
    
    .list {
        background: #fff;
        padding: 0 .2rem;
        font-size: .28rem;
        li {
            font-size: .32rem;
            height: .88rem;
            line-height: .88rem;
            border-bottom: 1px solid #eee;
            box-sizing: border-box;
            color: #000;
            display: flex;
            justify-content: space-between;
            input {
                outline: none;
                -webkit-appearance: none;
                border: none;
                font-size: .32rem;
            }
        }
    }
    
    .btn {
        background: #fff;
        text-align: center;
        padding-top: .3rem;
        padding-bottom: .28rem;
        button {
            font-size: .32rem;
            color: #fff;
            width: 80%;
            background: #3aacff;
            height: .7rem;
            border-radius: .1rem;
            outline: none;
            border: 0;
        }
    }
    
    .shop_list {
        width: 100%;
        font-size: .28rem;
        .list_info {
            position: relative;
            padding: .26rem 0 .26rem .54rem;
            border-bottom: 1px solid #eee;
            box-sizing: border-box;
            height: 1.65rem;
            display: flex;
             ::before {
                content: "";
                display: inline-block;
                width: .32rem;
                height: .32rem;
                border-radius: 50%;
                border: 2px solid #ccc;
                box-sizing: border-box;
                position: absolute;
                left: .05rem;
                top: 50%;
                -webkit-transform: translate3d(0, -50%, 0);
                transform: translate3d(0, -50%, 0);
            }
            .list_left {
                flex: 7;
                line-height: 2;
                display: flex;
                flex-direction: column;
                span {
                    font-size: .3rem;
                    &:nth-child(2) {
                        font-size: .24rem;
                        color: #666;
                    }
                }
            }
            .list_right {
                flex: 3;
                line-height: 2;
                display: flex;
                flex-direction: column;
                span {
                    color: red;
                    &:nth-child(2) {
                        font-size: .24rem;
                        color: #666;
                    }
                }
            }
        }
    }
    
    .button {
        border: 0;
        background: #eee;
        padding: 0 .2rem;
        color: #585858;
        height: .6rem;
        margin-top: .1rem;
        border-radius: .2rem;
    }
</style>


